@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
.home-content{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    max-width: 1100px;
    margin: 0 auto;
    color: #f1f1f1;
}

.title{
    width: 450px;
    margin-top: 12%;
    cursor: default;
}

.title span{
    transition: 0.5s;
}

.title span:hover{
    margin-left: 2%;
}

.title p{
    margin-top: 2%;
    font-size: 18px;
}

.social-medias{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.social-medias a{
    margin: 70px 40px 0 40px;
    padding: 30px 60px;
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #0e0e0e;
    border: 1px solid #0e0e0e;
    text-decoration: none;
    color: #f1f1f1;
    border-radius: 6px;
    transition: 0.5s;
    font-weight: bold;
    width: 210px;
}

.social-medias a:hover{
    border: 1px solid #944dff;
}

@media(max-width: 1060px){
    .home-content{
        height: 100%;
        padding: 0 0 80px 0;
    }

    .title{
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
    }

    .title span:hover{
        margin: 0;
    }
    
    .title p{
        margin-top: 2%;
        font-size: 18px;
    }

    .social-medias{
        flex-direction: column;
    }

    .social-medias a{
        width: 210px;
        text-align: center;
    }
}
header .menu-section{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: auto;
    position: fixed;
    top: 6%;
    left: 30%;
}

header nav ul{
    display: flex;
}

header nav ul li{
    list-style: none;
    padding: 0px 10px 0 10px;
}

header nav ul li .link{
    color: #f1f1f1;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.04rem;
    padding: 1.20rem;
    transition: all 500ms linear 0s;
    position: relative;
    display: block;
    border: 1px solid #080708;
}

header nav ul li .link:hover{
    border: 1px solid #944dff;
}

@media(max-width: 800px){
    nav{
        display: none;
    }

    .one,
    .two,
    .three{
        background-color: #f1f1f1;
        height: 5px;
        width: 100%;
        margin: 6px auto;
        transition-duration: 0.3s;
    }

    .menu-toggle{
        width: 40px;
        height: 30px;
        margin-right: 10px;
    }

    .menu-section.on{
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: #000;
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .menu-section.on nav{
        display: block;
    }

    .menu-section.on .menu-toggle{
        position: absolute;
        right: 25px;
        top: 15px;
    }

    .menu-section.on .menu-toggle .one {
        transform: rotate(45deg) translate(7px, 7px);
    }

    .menu-section.on .menu-toggle .two {
        opacity: 0;
    }

    .menu-section.on .menu-toggle .three {
        transform: rotate(-45deg) translate(8px, -9px);
    }

    .menu-section.on nav ul {
        text-align: center;
        display: block;
    }

    .menu-section.on nav ul a{
        transition-duration: 0.5s;
        font-size: 3rem;
        line-height: 4rem;
        display: block;
    }
}

main.about-content{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    max-width: 1100px;
    margin: 0 auto;
    color: #f1f1f1;
}

main.about-content h1{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 70px;
}

section{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

@-webkit-keyframes moveLeft {
    from {
      opacity: 0;
      transform: translateX(-35%);
    }
    to {
      opacity: 1;
      transform: translateX(0%);
    }
}

@keyframes moveLeft {
    from {
      opacity: 0;
      transform: translateX(-35%);
    }
    to {
      opacity: 1;
      transform: translateX(0%);
    }
}

@-webkit-keyframes moveRight {
    from {
      opacity: 0;
      transform: translateX(35%);
    }
    to {
      opacity: 1;
      transform: translateX(0%);
    }
}

@keyframes moveRight {
    from {
      opacity: 0;
      transform: translateX(35%);
    }
    to {
      opacity: 1;
      transform: translateX(0%);
    }
}

figure{
    overflow: hidden;
}

section .pic{
    width: 350px;
    -webkit-animation: moveLeft 1400ms;
            animation: moveLeft 1400ms;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    transition: 0.5s;
    overflow: hidden;
}

section .pic:hover{
    transform: scale(1.1);
    
}

section div.content{
    margin: 10px;
    width: 500px;
    text-align: justify;
    -webkit-animation: moveRight 1800ms;
            animation: moveRight 1800ms;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

section div.content p{
    padding: 10px;
}

section div.content .techs{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

section div.content .techs div{
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    align-items: center;
    justify-content: center;
    background-color: #0e0e0e;
    border: 1px solid #0e0e0e;
    border-radius: 8px;
    transition: 0.5s;
}

section div.content .techs div:nth-child(1){
    -webkit-animation: moveRight 1850ms;
            animation: moveRight 1850ms;
    -webkit-animation-delay: 600ms;
            animation-delay: 600ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

section div.content .techs div:nth-child(2){
    -webkit-animation: moveRight 1900ms;
            animation: moveRight 1900ms;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

section div.content .techs div:nth-child(3){
    -webkit-animation: moveRight 1950ms;
            animation: moveRight 1950ms;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

section div.content .techs div:nth-child(4){
    -webkit-animation: moveRight 2000ms;
            animation: moveRight 2000ms;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

section div.content .techs div:hover{
    cursor: default;
    border: 1px solid #944dff;
    color: #944dff;
}

section div.content .techs div .icon{
    font-size: 38px;
}

@media(max-width: 1060px){
    main.about-content{
        height: 100%;
        padding: 0 0 80px 0;
    }

    section{
        flex-direction: column;
        padding: 20px;
    }

    section .pic{
        margin-top: 12px;
        width: 260px;
    }

    section div.content{
        font-size: 1.1rem;
        overflow: hidden;
    }

    section div.content .techs div{
        border-color: #944dff;
    }
}

@media(max-width: 540px){
    section div.content{
        font-size: 1.1rem;
        width: 70%;
        overflow: hidden;
    }

    section div.content .techs{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    section div.content .techs div{
        width: 80%;
        margin: 10px;
    }
}

main{
    width: 100%;
    height: 100vh;
    max-width: 1100px;
    margin: 0 auto;
    color: #f1f1f1;
}

main.projects-content h1{
    display: flex;
    align-items: center;
    justify-content: center;
